/* ---------------------------------------------------------- */
/* base s =================================================== */
* {
	@include bxzb;
	outline: none;
	//font-weight: normal;
	font-style: normal;
}
html,body {
	width:100%;
	height:100%;
	&.back {
		background-color: #f5f5f5;
	}
}
body {
	position:relative;
	-webkit-font-smoothing: antialiased;
	font: {
	      family:$default_font, $second_font, 'malgun gothic', Dotum ,Gulim, Helvetica, sans-serif;
	      /*family: 'Lato', sans-serif;*/
				/*family: 'Nanum Gothic', sans-serif;*/
				/*family: 'Nanum Myeongjo', serif;*/
	      //size: 14px;
	      size: 62.5%;
	      weight: normal;
	   }
	color: $default_fc;
	&.hidden {
		overflow: hidden;
	}
}
a {
	text-decoration:none;
	color: $default_fc;
}
address,
em {
	font-style:normal;
}
hr {
	display:none;
}

/* table */
table {
	width:100%;
	border-collapse: separate;
}
th,
td {
	word-break:break-all;
	background-clip: padding-box;
	&.posr {
		position: relative;
	}
}
caption {
	overflow:hidden; 
	font-size:medium; 
	line-height:normal; 
	width:100%; 
	height:0; 
	word-break:break-all; 
	opacity:0; 
	filter:alpha(opacity=0); 
	-ms-filter:'progid:DXImageTransform.Microsoft.Alpha(opacity=0)';
}
fieldset {
	border:none;
}

/*  form */
input[type=radio],
input[type=checkbox] {
	@include css3-prefix(appearance, none); padding:0; border:0; background:none;
}
input[type=text],
input[type=password],
input[type=number] {

}
input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
	-webkit-appearance: none;
	margin: 0;
}
button {
	width:auto;
	border:0;
	padding: 0;
	background:none;
	font-weight:normal;
}
textarea {
	resize:none;
	border:1px solid #bfbfbf;
}
a,
img,
input,
button,
select,
textarea {
	vertical-align:middle;
}
a, button {
	&:focus, &:active, &:hover {
		text-decoration: none;
	}
}
ul, ol, dl {
	margin: 0;
	padding: 0;
}
li, dd {
	margin: 0;
	padding: 0;
	list-style: none;
}
p {
	margin: 0;
}
h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
	font-weight: normal;
	//line-height: 100%;
}
